

import { NavBar, MenuBar, BreadCrumb } from '@/components'
import { Component, Vue } from 'vue-property-decorator'

@Component({
  components: {
    MenuBar,
    NavBar,
    BreadCrumb
  }
})
export default class App extends Vue {
  data () {
    return {
      open: true,
      overlay: false,
      fullheight: true,
      fullwidth: false,
      right: false
    }
  }

  get isLoggedIn () {
    return this.$store.state.loggedIn
  }
}
